<template>
	<!--系统管理-->
	<div class="d-box v-box">
		<div class="headDiv">
			<div class="headerLeft">
				<span class="system">系统名称</span>
				<el-input placeholder="请输入内容" v-model="name" clearable>
				</el-input>
			</div>

			<div class="clearfix">
				<div style="float: right;">
					<SolidButton :type='"solid"' :text='"查询"' @click="see"></SolidButton>
					<SolidButton :type='"hollow"' :text='"重置"' @click="reset"></SolidButton>
				</div>
			</div>
			<div class="clearfix" style="padding-top: 20px;height: auto;">
				<Button :type='add.type' :icon='add.icon' :text='add.text' @click="addClick"></Button>
				<Button :type='out.type' :icon='out.icon' :text='out.text' @click="deleteClick"></Button>
				<Button :type='edit.type' :icon='edit.icon' :text='edit.text' @click="editClick"></Button>
			</div>
		</div>
		<div class="flexTable">
			<Table :data='rows' :tableName='tableName' @selection-change="handleSelectionChange" :currentPage1="currentPage1" :num='num' @size-change="handleSizeChange" @current-change="handleCurrentChange"></Table>
		</div>

		<el-dialog :title="title" :visible.sync="plus" width='600px' style='text-align: center;'>
			<el-form :model="form">
				<el-form-item label="系统名称" :label-width="formLabelWidth">
					<el-input v-model="form.name" placeholder="请输入系统名称" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="系统代号" :label-width="formLabelWidth">
					<el-input v-model="form.code" placeholder="请输入系统代号" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="系统描述" :label-width="formLabelWidth">
					<el-input v-model="form.description" placeholder="请输入系统描述" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<SolidButton :type='"hollow"' :text='"取消"' @click="plus = false"></SolidButton>
				<SolidButton :type='"solid"' :text='"确认"' @click="confirm"></SolidButton>
			</div>
		</el-dialog>
	</div>

</template>

<script>
	import Button from '../../button/Button.vue';
	import Table from '../../table/Table.vue';
	import SolidButton from '../../SelfControls/solidButton/solidButton.vue';

	export default {
		components: {
			Button,
			Table,
			SolidButton
		},
		data() {
			return {
				num: 0, //jxs会报一个类型错误，就设了一个数字初始值
				currentPage1: 1,
				rows: [],
				name: '',
				plus: false,
				list: [],
				title: '',
				ids: '',
				followed: '',
				pageNum: '1',
				pageRows: '10',
				form: {
					name: '',
					code: '',
					description: '',
				},
				formLabelWidth: '77px',
				add: {
					text: '新增',
					type: 'icon-add',
					icon: 'el-icon-plus',
				},
				out: {
					text: '删除',
					type: 'icon-delete',
					icon: 'el-icon-delete',
				},
				edit: {
					text: '编辑',
					type: 'icon-edit',
					icon: 'el-icon-edit',
				},
				//表格对应的字段
				tableName: [{
						value: 'name',
						name: "系统名称"
					},
					{
						value: 'code',
						name: "系统代号"
					},
					{
						value: 'description',
						name: '系统描述'
					}
				]
			}
		},
		methods: {
			//新增
			addClick() {
				this.title = '新增子系统';
				this.plus = true;
				this.form.name = '';
				this.form.code = '';
				this.form.description = '';
				delete this.form.id;
				this.followed = true;
			},
			//删除
			deleteClick() {
				if(this.list.length == 0) {
					this.$message({
						message: '未勾选数据',
						type: 'warning'
					});
				} else {
					this.$confirm('此操作将永久删除该系统, 是否继续?', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
					}).then(() => {
						this.ids = '';
						for(var i = 0; i < this.list.length; i++) {
							this.ids += this.list[i].id + ','
						}
						var p = {
							ids: this.ids.substring(0, this.ids.length - 1)
						}
						this.$api.post('sys/delete', p, r => {
							this.$message({
								message: '删除成功',
								type: 'success'
							});
							this.loadData();
						}, e => {
							console.log(e);
						})
					}).catch(() => {
						this.$message({
							type: 'info',
							message: '已取消删除'
						});
					});
				}
			},
			//编辑
			editClick() {

				if(this.list.length == 0) {
					this.$message({
						message: '未勾选数据',
						type: 'warning'
					});
				} else if(this.list.length > 1) {
					this.$message({
						message: '请勾选单条数据',
						type: 'warning'
					});
				} else {
					this.title = '修改子系统';
					this.plus = true;
					this.form.name = this.list[0].name;
					this.form.code = this.list[0].code;
					this.form.description = this.list[0].description;
					this.form.id = this.list[0].id;
				}

			},
			//表格Checkbox 选中
			handleSelectionChange(val) {
				this.list = val;
				console.log(this.list)
			},
			//初始表格查询
			loadData() {
				var self = this;
				var p = {
					name: self.name,
					type: "1",
					pageNum: self.pageNum,
					pageRows: self.pageRows,
				}
				self.$api.post('sys/all', p, r => {
					self.rows = r.repData.rows
					self.num = r.repData.rows.length
				}, e => {
					this.$message({
						message: e.repData.repMsg,
						type: 'error'
					});
				})
			},
			//分页
			handleSizeChange(val) {
				this.pageRows = val;
				this.loadData();
			},
			handleCurrentChange(val) {
				this.pageNum = val;
				this.loadData();
			},
			//查询
			see() {
				this.loadData();
			},
			//重置
			reset() {
				this.name = '';
				this.loadData();
			},
			//新增确认
			confirm() {
				if(this.form.name == '') {
					this.$message({
						message: '系统名称不能为空',
						type: 'warning'
					});
					return;
				} else if(this.form.code == '') {
					this.$message({
						message: '系统代号不能为空',
						type: 'warning'
					});
					return;
				} else if(this.form.description == '') {
					this.$message({
						message: '系统描述不能为空',
						type: 'warning'
					});
					return;
				}
				var str = '';
				var url = '';
				if(this.title == '新增子系统') {
					str = '新增成功';
					url = 'sys/add';
				} else if(this.title == '修改子系统') {
					str = '修改成功';
					url = 'sys/update';
				}
				this.$api.post(url, this.form, r => {
					this.$message({
						message: str,
						type: 'success'
					});
					this.plus = false;
					this.loadData();
				}, e => {
					console.log(e);
				})

			}
		},
		created: function() {
			this.loadData();
		},
	}
</script>

<style scoped="scoped">
	/*head-left内容*/
	
	.headerLeft {
		width: 294px;
		height: 40px;
		float: left;
	}
	
	.headerLeft span {
		width: 64px;
	}
	
	.headerLeft .el-input {
		width: 188px;
		height: 37px;
	}
	
	.system {
		display: inline-block;
		width: 100px;
		line-height: 40px;
		text-align: center;
	}
	
	.right {
		display: inline-block;
		margin-left: 20px;
		float: right;
	}
	
	.dialog-footer {
		text-align: center;
	}
	
	.d-box {
		display: -moz-box;
		/* OLD - Firefox 19- (buggy but mostly works) */
		display: -webkit-box;
		/* OLD - iOS 6-, Safari 3.1-6 */
		display: -webkit-flex;
		/* NEW - Chrome */
		display: -ms-flexbox;
		/* TWEENER - IE 10 */
		display: box;
		display: flexbox;
		display: flex;
		/* NEW, Spec - Opera 12.1, Firefox 20+ */
		height: 100%;
		width: 100%;
	}
	
	.v-box {
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-moz-box-orient: vertical;
		-moz-box-direction: normal;
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}
	
	.headDiv {
		padding: 20px;
		background: #fff;
		overflow: hidden;
		position: relative;
	}
	
	.flexTable {
		-webkit-box-flex: 1;
		-moz-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
		position: relative;
	}
	
	.tableDiv {
		width: 100%;
		height: 100%;
		position: absolute;
		overflow: auto;
	}
	
	.footerDiv {
		height: 40px;
		position: relative;
	}
</style>


